$color: var(--primary-color) !default;
$text-color: white !default;

.hide-type .codenumber:not(:empty)::after {
    content: " -";
}

section > .heading.hide-type {
  background: $color;
  padding: 5px;
  padding-left: 15px;
  color: $text-color;
}

section.section > .heading{
  background-color: var(--primary-color-black-20);
}

// Top heading followed by another heading
.section > .heading + * > .heading {
  margin-top: 0.5em;
}
