// based palette for toc/buttons/etc...
@use 'colors/palette-single' as palette-minimal with (
    $primary-color: $color1,
);

// fancy colors for chunks
@use 'colors/palette-chunks-quad' as palette-chunks with (
  $color1: color.scale($color1, $lightness: 25%),
  $color2: $color2,
  $color3: $color3,
  $color4: $color4,
  $heading-text-color: colorHelpers.text-contrast($color2),
);

$light-colors: map.merge(palette-minimal.$colors, palette-chunks.$colors);

// extra tweaks
$light-colors: map.merge($light-colors, (
    "chaptertoc": var(--mainbackground),
    "sectiontoc": var(--primary-color-white-96),
    "parttoc": var(--mainbackground),
    "tocborder": var(--primary-color-white-90),
    "parttocactive": var(--primary-color-white-90),
    "parttoctextactive": var(--parttoctext),
    "permalink-opacity": 0,
));


@use 'colors/palette-dark' as palette-dark with (
    $primary-color: $primary-color-dark,
    $background-color: $background-color-dark,
);

$palette-dark: map.merge(palette-dark.$colors, (
  "chaptertoc": var(--mainbackground),
  "sectiontoc": var(--background-color-gray-20),
  "parttoc": var(--mainbackground),
));



:root {
    color-scheme: light;
    &:not(.dark-mode) {  // prevent variable leak through to dark
      @include colorHelpers.scss-to-css($light-colors);
    }
}

:root.dark-mode {
    color-scheme: dark;
    @include colorHelpers.scss-to-css($palette-dark);
}